:root {
  --background: #0f172a;
  --foreground: #f8fafc;
  --primary: #3b82f6;
  --primary-hover: #2563eb;
  --secondary: #1e293b;
  --border: #334155;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: var(--background);
  color: var(--foreground);
}

.optidrive-container {
  display: flex;
  min-height: 100vh;
  background: linear-gradient(135deg, #0f172a 0%, #1e3a8a 100%);
}

.sidebar {
  width: 250px;
  background-color: var(--secondary);
  padding: 1rem;
  transition: width 0.3s ease;
}

.sidebar-group-label {
  font-size: 0.75rem;
  text-transform: uppercase;
  color: #94a3b8;
  margin-bottom: 0.5rem;
}

.sidebar-menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.sidebar-menu-button {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  color: var(--foreground);
  background: none;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s;
}

.sidebar-menu-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.sidebar-menu-button svg {
  margin-right: 0.5rem;
}

.main-content {
  flex: 1;
  padding: 2rem;
  transition: padding 0.3s ease;
}

h1 {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.action-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4rem;
  font-size: 1.125rem;
  font-weight: bold;
  color: var(--foreground);
  background-color: var(--primary);
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
}

.action-button:hover {
  background-color: var(--primary-hover);
  transform: scale(1.05);
}

.action-button svg {
  margin-right: 0.5rem;
}

.drop-zone {
  border: 2px dashed var(--border);
  border-radius: 0.5rem;
  padding: 2rem;
  text-align: center;
  transition: background-color 0.2s, transform 0.2s;
}

.drop-zone:hover {
  background-color: rgba(255, 255, 255, 0.05);
  transform: scale(1.05);
}

.drop-zone p {
  font-size: 1.25rem;
  margin-bottom: 1rem;
}

.file-input-label {
  display: inline-block;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: bold;
  color: var(--foreground);
  background-color: var(--primary);
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
}

.file-input-label:hover {
  background-color: var(--primary-hover);
  transform: scale(1.05);
}

.glow-effect {
  pointer-events: none;
  position: absolute;
  inset: 0;
  z-index: 30;
  transition: opacity 0.3s;
  background: radial-gradient(
          circle 200px at var(--mouse-x) var(--mouse-y),
          rgba(59, 130, 246, 0.15),
          transparent 80%
  );
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@media (max-width: 768px) {
  .sidebar {
    width: 60px;
    padding: 0.5rem;
  }

  .sidebar-group-label {
    display: none;
  }

  .sidebar-menu-button {
    justify-content: center;
  }

  .sidebar-menu-button svg {
    margin-right: 0;
  }

  .main-content {
    padding: 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .action-grid {
    grid-template-columns: 1fr;
  }

  .action-button {
    height: 3rem;
    font-size: 1rem;
  }

  .drop-zone {
    padding: 1rem;
  }

  .drop-zone p {
    font-size: 1rem;
  }

  .file-input-label {
    padding: 0.5rem;
    font-size: 0.875rem;
  }
}

